body {
  font-family: 'Helvetica', Arial, sans-serif;
  font-size: 100%;
  line-height: 1.4; }

h2 {
  font-size: 1em;
  font-weight: bold;
  margin: 0 0 0.25em; }

* {
  text-rendering: auto !important;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent; }

@-webkit-keyframes scaleOut {
  from {
    -webkit-transform: scale(1);
    opacity: 1; }

  to {
    -webkit-transform: scale(0.8);
    opacity: 0; } }

@-moz-keyframes scaleOut {
  from {
    -moz-transform: scale(1);
    opacity: 1; }

  to {
    -moz-transform: scale(0.8);
    opacity: 0; } }

@keyframes scaleOut {
  from {
    transform: scale(1);
    opacity: 1; }

  to {
    transform: scale(0.8);
    opacity: 0; } }

@-webkit-keyframes superScaleIn {
  from {
    -webkit-transform: scale(1.2);
    opacity: 0; }

  to {
    -webkit-transform: scale(1);
    opacity: 1; } }

@-moz-keyframes superScaleIn {
  from {
    -moz-transform: scale(1.2);
    opacity: 0; }

  to {
    -moz-transform: scale(1);
    opacity: 1; } }

@keyframes superScaleIn {
  from {
    transform: scale(1.2);
    opacity: 0; }

  to {
    transform: scale(1);
    opacity: 1; } }

/**
 * Popups
 * --------------------------------------------------
 */
.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1500;
  border: 1px solid #eee;
  visibility: hidden;
  width: 80%;
  max-height: 80%;
  overflow: hidden;
  border-radius: 15px;
  background-color: rgba(235, 235, 235, 0.95); }
  .popup.popup-hidden {
    -webkit-animation-name: scaleOut;
    -moz-animation-name: scaleOut;
    animation-name: scaleOut;
    -webkit-animation-duration: 0.1s;
    -moz-animation-duration: 0.1s;
    animation-duration: 0.1s;
    -webkit-animation-timing-function: ease-in-out;
    -moz-animation-timing-function: ease-in-out;
    animation-timing-function: ease-in-out;
    -webkit-animation-fill-mode: both;
    -moz-animation-fill-mode: both;
    animation-fill-mode: both; }
  .popup.popup-showing {
    visibility: visible; }
  .popup.active {
    -webkit-animation-name: superScaleIn;
    -moz-animation-name: superScaleIn;
    animation-name: superScaleIn;
    -webkit-animation-duration: 0.2s;
    -moz-animation-duration: 0.2s;
    animation-duration: 0.2s;
    -webkit-animation-timing-function: ease-in-out;
    -moz-animation-timing-function: ease-in-out;
    animation-timing-function: ease-in-out;
    -webkit-animation-fill-mode: both;
    -moz-animation-fill-mode: both;
    animation-fill-mode: both; }

.popup-box {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-direction: normal;
  -webkit-box-orient: vertical;
  -webkit-flex-direction: column;
  -moz-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  padding-top: 10px; }

.popup-head {
  padding: 10px 0px;
  border-bottom: 1px solid #eee;
  text-align: center; }

.popup-title {
  margin: 0;
  padding: 0;
  font-size: 15px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.popup-sub-title {
  margin: 5px 0 0 0;
  padding: 0;
  font-weight: normal;
  font-size: 11px; }

.popup-body {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  -moz-box-flex: 1;
  -moz-flex: 1;
  -ms-flex: 1;
  flex: 1;
  overflow: scroll;
  padding: 0 10px; }
  .popup-body .popup-icon {
    font-size: 30px;
    text-align: center; }
  .popup-body .popup-text {
    text-align: center; }

.popup-limit, .popup-prevent {
  text-align: center;
  margin-bottom: 10px; }

.popup-buttons {
  text-align: center; }
  .popup-buttons.row {
    padding: 10px 10px; }
  .popup-buttons .btn {
    margin: 0px 5px;
    padding: 0 16px;
    min-height: 45px;
    border-radius: 5px;
    line-height: 20px;
    background-color: rgba(255, 255, 255, 0.00784314);
    border: 1px solid #4c4c4c;
    -webkit-border-radius: 4px;
    border-radius: 4px;
    -webkit-box-shadow: 0 2px 0 0 #9b9b9b;
    box-shadow: 0 2px 0 0 #9b9b9b;
    -webkit-user-select: none;
    background: #d4d4d4;
    border: none;
    color: #4a4a4a;
    display: inline-block;
    font-weight: bold;
    padding: 3px 10px 4px;
    position: relative;
    text-align: center;
    top: -2px;
    border-top: 2px solid transparent;
    text-decoration: none;
    outline: 0; }
    .popup-buttons .btn:active, .popup-buttons .btn.active {
      -webkit-box-shadow: none;
      box-shadow: none;
      background: #bbb;
      border-top: 2px solid #9b9b9b;
      top: 0px; }
    .popup-buttons .btn:first-child {
      margin-left: 0px; }
    .popup-buttons .btn:last-child {
      margin-right: 0px; }
    .popup-buttons .btn.btn-main {
      -webkit-border-radius: 4px;
      border-radius: 4px;
      -webkit-box-shadow: 0 2px 0 0 #5d0662;
      box-shadow: 0 2px 0 0 #5d0662;
      -webkit-user-select: none;
      background: #a40aaa;
      border: none;
      color: white;
      display: inline-block;
      font-weight: bold;
      padding: 3px 10px 4px;
      position: relative;
      text-align: center;
      top: -2px;
      border-top: 2px solid transparent;
      text-decoration: none;
      outline: 0; }
      .popup-buttons .btn.btn-main:active, .popup-buttons .btn.btn-main.active {
        -webkit-box-shadow: none;
        box-shadow: none;
        background: #8b0992;
        border-top: 2px solid #5d0662;
        top: 0px; }

.popup-open {
  pointer-events: none; }
  .popup-open.modal-open .modal {
    pointer-events: none; }
  .popup-open .popup-backdrop, .popup-open .popup {
    pointer-events: auto; }
